import React from 'react';
import {connect} from "react-redux";
import {getFriendsThunkCreator} from "../../redux/sidebar-reducer";
import Navbar from "./Navbar";
import {needUpdateFriendsAC, unfollowThunkCreator} from "../../redux/users-reducer";
import {bedug_mode} from "../../redux/store-redux";
import ErrorBoundary from "../common/ErrorBoundary/ErrorBoundary";


class NavBarContainer extends React.Component {
    componentDidMount() {
        const {
            friendsCurrentPage, friendsPageSize,
            friendsTerm, friend, getFriendsThunkCreator
        } = this.props; //      
        getFriendsThunkCreator(friendsCurrentPage, friendsPageSize, friendsTerm, friend); //    
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.needUpdateFriends) {
            const {
                friendsCurrentPage, friendsPageSize,
                friendsTerm, friend, getFriendsThunkCreator
            } = this.props; //      
            getFriendsThunkCreator(friendsCurrentPage, friendsPageSize, friendsTerm, friend); //    
            this.props.needUpdateFriendsAC(false)
        }
    }

    unfollowFriendsAPI = (id) => { //     Friends List    id    
        this.props.unfollowThunkCreator(id, this.props.currentPage, this.props.pageSize, this.props.term)
    }

    render() {
        if (bedug_mode) {
            console.log("NavBarContainer render")
        }

        const {myFriends2} = this.props; //      
        return <ErrorBoundary> {/*   Navbar*/}
            <Navbar
                myFriends2={myFriends2}
                unfollowFriendsAPI={this.unfollowFriendsAPI}
                dialogUserID={this.props.dialogUserID}
            /> {/*  */}
        </ErrorBoundary>
    }
}

const mapStateToProps = (state) => {
    return {
        myFriends2: state.sideBar.myFriends2, //   
        friendsCurrentPage: state.sideBar.friendsCurrentPage, //    
        friendsPageSize: state.sideBar.friendsPageSize, //       
        friendsTerm: state.sideBar.friendsTerm, //   
        friend: state.sideBar.friend, //    ( ,  follow = true)
        needUpdateFriends: state.usersPage.needUpdateFriends, //      
//       FriendList    users
        currentPage: state.usersPage.currentPage, //   users  
        pageSize: state.usersPage.pageSize, //      
        term: state.usersPage.term, //    users  
//         FriendList
        dialogUserID: state.dialogsPage.dialogUserID // ID  ,    

    }
}
export default connect(mapStateToProps, {
    getFriendsThunkCreator,
    needUpdateFriendsAC,
    unfollowThunkCreator
})(NavBarContainer);









