import React from 'react'; //  
import classes from './MyPosts.module.css' // css 
import Post from "./Post/Post"; //     map
import MyPostsFormik from "./Post/MyPostsFormik/MyPostsFormik";
import {bedug_mode} from "../../../redux/store-redux"; // reduxForm    

const MyPosts = ({userId, state, addPost, dispatch}) => { //    
    if (bedug_mode) {
        console.log("MyPosts.jsx")
    } // 

    let postElements = state.posts.map((p) => //      map
        <Post key={p.id} message={p.message} like={p.like} id={p.id}/>);

    return (
        <div className={classes.postsBlock} /**/ >
            {(userId === 0) && //       (   ID  profile)
            <div>
                <fieldset>
                    <legend>
                        <div className={classes.legendStyle}> </div>
                    </legend>
                    <MyPostsFormik
                        addPost={addPost}/> {/*        */}
                    <div className={classes.posts}>
                        {postElements} {/* */}
                    </div>
                </fieldset>
            </div>
            }
        </div>
    )
}
export default MyPosts;
