import React, {useState} from 'react';
import classes from './ProfileInfo.module.css'
import commonClasses from '../../common/ButtonOverImage/ButtonOverImage.module.css'
import Preloader from "../../common/Preloader/Preloader";
import ProfileStatusUseReducer from "./ProfileStatus/ProfileStatusUseReducer";
import {bedug_mode} from "../../../redux/store-redux";
import userPhoto1 from "../../../assets/images/no-image3.png";
//import EditProfile from "./EditProfile/EditProfile";
import EditProfileFormik from "./EditProfile/EditProfileFormik"; //   

const ShowProfile = ({profile, setEditMode, userId, myId}) => { //    
    let Contact = (key1) => { /*     contacts  profile*/
        return <div>
            <b>{key1}: </b>{profile.contacts[key1]}
        </div>
    }
    return (<div>
            <div className={classes.profilefullName}>{profile.fullName}</div>
            <div><b> </b>: {profile.aboutMe}</div>
            <div><b>  ?</b> {profile.lookingForAJob ? "" : ""}</div>
            <div><b>  :</b> {profile.lookingForAJobDescription}</div>
            <div><b>userId:</b> {profile.userId}</div>
            <div><b>:</b></div>
            <div className={classes.ProfileContacts}>
                {Object.keys(profile.contacts).map((key1) => { //
                    return (<div key={key1}>
                            {Contact(key1)}
                        </div>
                    )
                })}
            </div>
            {(userId === 0) && <button onClick={() => {
                setEditMode(true)
            }}>  </button>}

        </div>
    )
}

const ProfileInfo = ({profile, myId, status, putStatusThunkCreator, uploadImage, userId, putProfile, dispatch}) => {
    const [profilePhoto, setprofilePhoto] = useState(userPhoto1) // useState     
    const [editMode, setEditMode] = useState(false) //    
    const [showUploadImageButton, setshowUploadImageButton] = useState(false) //      
    if (bedug_mode) {
        console.log("ProfileInfo.jsx")
    } // 
    if (!profile) { //     
        return <Preloader/> //  
    }
    let onChangeProfilePhoto = (e) => {
        setprofilePhoto(e.target.files[0]) //   useState    ( )
    }
    let displayClass = showUploadImageButton ? "" : commonClasses.displayNone //  /     

    let profileStatus = <ProfileStatusUseReducer //    ProfileStatusUseState  ProfileStatusClass
        myId={myId} //  id   
        userId={profile.userId} // id  
        status={status} //   BLL
        putStatusThunkCreator={putStatusThunkCreator} //    
    />

    let showProfile = !editMode &&
        <ShowProfile profile={profile} setEditMode={setEditMode} userId={userId} myId={myId}/>

    let editProfile = editMode &&
        <div>
{/*
            <EditProfile profile={profile} putProfile={putProfile} dispatch={dispatch} setEditMode={setEditMode}
                         userId={userId} myId={myId}/>
*/}

            <EditProfileFormik profile={profile} putProfile={putProfile} dispatch={dispatch} setEditMode={setEditMode}
                               userId={userId} myId={myId}/>
        </div>
    let editMyPhoto = (userId === 0) &&//       (   ID  profile)
        <div>
            <form> {/*     */}
                <span><button
                    className={commonClasses.btn1 + " " + displayClass} //   -    +  /  
                    onMouseOver={() => {
                        setshowUploadImageButton(true)
                    }} //      setshowUploadImageButton  true ( )
                    onMouseOut={() => {
                        setshowUploadImageButton(false)
                    }}//       setshowUploadImageButton  false ( )
                    onClick={() => { //
                        uploadImage(profilePhoto)
                    }}></button></span> {/*     */}
                <span>
                    <input
                        className={commonClasses.btn2 + " " + displayClass} //   -    +  /  
                        onMouseOver={() => {
                            setshowUploadImageButton(true)
                        }} //      setshowUploadImageButton  true ( )
                        onMouseOut={() => {
                            setshowUploadImageButton(false)
                        }}//       setshowUploadImageButton  false ( )
                        type="file" onChange={onChangeProfilePhoto}/></span> {/* */}
            </form>
        </div>

    let showUserPhoto = <img
        alt={"userPhoto"}
        onMouseOver={() => {
            setshowUploadImageButton(true)
        }} //  
        onMouseOut={() => {
            setshowUploadImageButton(false)
        }}
        className={commonClasses.profilePhotoIMG}
        src={profile.photos.large ? profile.photos.large : userPhoto1}/>

    return <div>
        <div className={classes.profileInfoGreed}>
            <div className={commonClasses.container}>
                <div>{showUserPhoto}</div>
                {/*  */}
                <div> {editMyPhoto} </div>
                {/*  ,    */}
            </div>

            <div>
                <div>
                    {showProfile} {/* */}
                    {editProfile} {/* */}
                </div>
                <div>
                    {profileStatus} {/*  */}
                </div>
            </div>
        </div>

    </div>
}
export default ProfileInfo;
