import React from "react";
import {Formik, Form} from "formik"; //     
import * as Yup from 'yup' //       Yup
import classes from './EditProfile.module.css'
//import DisplayFormikState from "../../../common/formikCommon/DisplayFormikState"
import {MyCheckbox, MyTextArea, MyTextInput} from "../../../common/formikCommon/MyFields"

let myValidationSchema = Yup.object({ //    required,    
})

const EditProfileFormik = ({putProfile, setEditMode, profile, userId}) => { //     ,     

    let myInitialValues = { //   
        FullName: profile.fullName,
        AboutMe: profile.aboutMe,
        LookingForAJob: profile.lookingForAJob,
        LookingForAJobDescription: profile.lookingForAJobDescription,
        contacts: profile.contacts, //     
    }

    const myOnSubmit = (values, {resetForm}) => { //   
        putProfile(values) // ,     
        resetForm()//     
        setEditMode(false) //       

    }

    return (
        <>
            <Formik
                initialValues={myInitialValues} //   
                validationSchema={myValidationSchema} //  
                onSubmit={myOnSubmit} //   
            >

                {({
                      handleReset,//  
                  }) => ( //           (  )
                    <Form className={classes.MyPosts}>
                        <div>
                            <div className={classes.HeaderEditProfileForm}> </div>
                            <MyTextInput //  
                                label=" :" name='FullName' type='text' placeholder=' '
                            />

                            <MyTextInput // 
                                label=" :" name='AboutMe' type='text' placeholder=' '
                            />

                            <MyCheckbox name='LookingForAJob'>  {/*  */}
                                  
                            </MyCheckbox>

                            <MyTextArea //  
                                label="  :" name='LookingForAJobDescription'
                                type='textarea' placeholder='  '
                            />

                            <h4>:</h4> {/*    */}
                            <div className={classes.EditProfileContactsFields}>
                                {Object.keys(profile.contacts).map((c) => { //   
                                    return <div key={c} >
                                        <MyTextInput  //  
                                            label={c} name={`contacts[`+c+']'} type='text' placeholder={c}
                                        />
                                    </div>
                                })}
                            </div>
                            <br/>
                            <button type='button' onClick={handleReset}></button>
                            {/*     */}
                            {" "}
                            <button type="submit"></button> {/*  */}

                             <button type="button" onClick={() => {setEditMode(false)}}></button>  {/**/}

                        </div>
                        {/*   <DisplayFormikState/> {/*   */}
                    </Form>
                )}
            </Formik>
        </>
    )
}

export default EditProfileFormik

