import React, {useReducer} from "react";
import {bedug_mode} from "../../../../redux/store-redux";

let ProfileStatusUseReducer = ({status, userId, myId, putStatusThunkCreator}) => {
    const initialState = {
        modifyStatus2: false,//  -  
        statusTmpInput2: null //       ( input)
    }

    const SET_MODIFY_STATUS_TRUE = "SET_MODIFY_STATUS_TRUE"; //       (modify true)
    const SET_MODIFY_STATUS_FALSE = "SET_MODIFY_STATUS_FALSE";//       (modify false)
    const SET_STATUS_TMP_INPUT = "SET_STATUS_TMP_INPUT";//       (  input)

    const localReducer = (localState, action) => {
        let stateCopy; //    
        switch (action.type) {
            case SET_MODIFY_STATUS_TRUE: //     input ( )
                stateCopy = {
                    ...localState,
                    modifyStatus2: true, //    true
                    statusTmpInput2: status//    input   
                };
                return stateCopy; //      
            case SET_MODIFY_STATUS_FALSE: //     
                stateCopy = {
                    ...localState,
                    modifyStatus2: false //    false
                };
                return stateCopy; //      
            case 'SET_STATUS_TMP_INPUT':
                stateCopy = {
                    ...localState,
                    statusTmpInput2: action.text //     value input
                };
                return stateCopy; //      
            default:
                throw new Error(); //   
        }
    }

    const [localState, dispatch] = useReducer(localReducer, initialState)//   useState  useReducer

    const checkIfICanModifyStatus = () => {// ,      (    )
        if (userId === myId) { //  ID    
            dispatch({type: SET_MODIFY_STATUS_TRUE})//       input
        }
    }
    const setMyStatus = () => { //       input    Enter
        dispatch({type: SET_MODIFY_STATUS_FALSE})//       input

        if (bedug_mode) {console.log("ProfileStatusUseReducer.jsx, setMyStatus putStatusThunkCreator() ->SET_USER_PROFILE" )} // 

        /////////  ProfileInfo.jsx

        putStatusThunkCreator(localState.statusTmpInput2, myId)//      
    }
    const onChangeStatus = (event) => {
        const text = event.currentTarget.value;//       input
        dispatch({type: SET_STATUS_TMP_INPUT, text: text})//           
    }
    const checkEnterPressed = (event) => { //   Enter
        if (event.charCode === 13) {
            setMyStatus()//    Enter
        }
    }

    return (<div>
        {!localState.modifyStatus2
            ? <div>
                <h3
                    onDoubleClick={checkIfICanModifyStatus}> {/*           */}
                    : {!status //        
                        ? " " //  " "
                        : status //     BLL,  
                    }
                </h3>
            </div>
            : <h3>
                <span onDoubleClick={setMyStatus}>
                    <input
                        value={localState.statusTmpInput2} //            
                        onChange={onChangeStatus} //    
                        onBlur={setMyStatus}//      input
                        autoFocus //     
                        placeholder={" "}//     
                        onKeyPress={checkEnterPressed} //   Enter
                    />
                </span>
            </h3>
        }
    </div>)
}
export default ProfileStatusUseReducer







