import React from 'react';
import classes from './Profile.module.css'
import ProfileInfo from "./ProfileInfo/ProfileInfo";
import MyPostsContainer from "./MyPosts/MyPostsContainer";
import {bedug_mode} from "../../redux/store-redux";
import ScrollContainer from "../common/Scroll/ScrollContainer";

const Profile = ({profile, status, myId, putStatusThunkCreator, uploadImage, userId, putProfile, dispatch}) => {
    if (bedug_mode) {
        console.log("Profile.jsx")
    } // 

    let ProfileRender = <div className={classes.ProfileRender}>
        <ProfileInfo //    
            profile={profile} //   
            status={status} //   BLL
            myId={myId} //  id   
            putStatusThunkCreator={putStatusThunkCreator} //    
            uploadImage={uploadImage} //      
            userId={userId} // id  ,   URL
            putProfile={putProfile}
            dispatch={dispatch}
        />
        <MyPostsContainer //    (    BLL)
            userId={userId} // id  ,   URL
        />
    </div>
    return <div>
        <ScrollContainer
            child={ProfileRender}
            height={window.screen.availHeight - 218}
            firstInsideContainer={"ProfileUp"}
            secondInsideContainer={"ProfileDown"}
            containerElement={"ProfileContainer"}
        /> {/* FriendList   */}
    </div>
}
export default Profile;
