import React from "react";
import {Formik, Form} from "formik"; //     
import * as Yup from 'yup' //       Yup
import classes from "./MyPosts.module.css"
//import DisplayFormikState from "../../../../common/formikCommon/DisplayFormikState"
import {MyTextArea} from "../../../../common/formikCommon/MyFields"

let myInitialValues = { //   
    newPost: "",
}
let myValidationSchema = Yup.object({ //    required,    
})

const MyPostsFormik  = ({addPost}) => { //     ,     
    const myOnSubmit = (values, {resetForm}) => { //   
        addPost(values.newPost) // ,     
        resetForm()//     
    }

    return (
        <>
            <Formik
                initialValues={myInitialValues}
                validationSchema={myValidationSchema}
                onSubmit={myOnSubmit}
            >

                {({
                      handleReset,//  
                  }) => ( //           (  )
                    <Form className={classes.MyPosts}>
                        <div>
                            <MyTextArea //   MyPosts
                                label=""
                                name='newPost'
                                type='textarea'
                                placeholder='  '
                            />

                            <button type="submit" > {/*  */}
                                Submit
                            </button>
                            <button type='button' onClick={handleReset}>Reset</button>
                            {/*     */}
                        </div>
                        <div/>
                        {/*  <DisplayFormikState/> {/*   */}
                    </Form>

                )}

            </Formik>
        </>
    )
}

export default MyPostsFormik

