import {useField} from "formik"; //     
import classes from "./formik1.module.css"

export const CommonInputTextArea = ({label, children, ...props}) => {
    const [meta] = useField(props.props)  //  onBlur  meta   
    return (
        <>
            <label
                htmlFor={props.props.id || props.props.name}>{label}</label> {/*   props.id (  ),   props.name*/}
            {children} {/*input  textarea*/}
            {meta.touched && meta.error && //       
            <div className={classes.errorText}>{meta.error}</div>} {/*  */}
        </>
    )
}
export const MyTextInput = ({label, ...props}) => {  //      
    const [field, meta] = useField(props) //  onBlur  meta   

    return (
        <CommonInputTextArea label={label} props={props}> {/*    InputTextArea*/}
            <input
                className={meta.touched && meta.error ? classes.errorInputTextArea : classes.inputTextArea}  {...field} {...props}/> {/*   input  textarea*/}
        </CommonInputTextArea>
    )
}

export const MyTextArea = ({label, ...props}) => {  //      
    const [field, meta] = useField(props)  //  onBlur  meta   
    return (
        <CommonInputTextArea label={label} props={props}>{/*    InputTextArea*/}
            <textarea
                className={meta.touched && meta.error ? classes.errorInputTextArea : classes.inputTextArea} {...field} {...props}/> {/*   input  textarea*/}
        </CommonInputTextArea>
    )
}


export const MyCheckbox = ({children, ...props}) => {
    const [field, meta] = useField({...props, type: 'checkbox'}) //  onBlur  meta   
    return (
        <div>
            <label className='checkbox-input'>
                <input type='checkbox' {...field} {...props}/> {/* */}
                {children}{/*  I accept the terms and conditions*/}
            </label>
            {meta.touched && meta.error && //     
            <div className={classes.errorText}>{meta.error}</div>}
        </div>
    )
}

export const MySelect = ({label, ...props}) => {
    const [field, meta] = useField(props) //  onBlur  meta   
    return (
        <div>
            <label htmlFor={props.id || props.name}>{label}</label> {/* select*/}
            <select {...field} {...props}/>{/*  select  option*/}
            {meta.touched && meta.error && //     
            <div className={classes.errorText}>{meta.error}</div>} {/*  */}
        </div>
    )
}
